import * as actionTypes from './actionTypes';
import axios from 'axios';

export const authStart=()=>{

    return{

        type:actionTypes.AUTH_START
        
    }
}

export const authSuccess=(token,userId)=>{

    return{

        type:actionTypes.AUTH_SUCCESS,
        idtoken:token,
        userId:userId
        
    }
}


export const authFail=(err)=>{

    return{

        type:actionTypes.AUTH_FAIL,
        error:err   
    }
}

export const logout=()=>{

    localStorage.removeItem('token');
    localStorage.removeItem('expiresIn');
    localStorage.removeItem('userId')
    console.log('222222222222222222222222222');

    return{
        type:actionTypes.AUTH_LOGOUT
    }
}

export const checkAuthTimeout=(expirationTime)=>{

    return dispatch=>{

        setTimeout(()=>{
            console.log('33333333333333333333333333333');
            dispatch(logout())
        },expirationTime*1000)

    }
}

export const auth=(email,password,isSignUp)=>{

    return(dispatch)=>{
        
        let url='https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyDZB8vlp1JMnbLLrI4hZrJMv4EdTBhejgA';

        if(!isSignUp){
                url='https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyDZB8vlp1JMnbLLrI4hZrJMv4EdTBhejgA'
        }
        dispatch(authStart());
        
        const authData={
            email:email,
            password:password,
            returnSecureToken:true
        }

        axios.post(url,authData)
        .then(resp=>{
            console.log(resp);
            dispatch(authSuccess(resp.data.idToken,resp.data.localId))
            dispatch(checkAuthTimeout(resp.data.expiresIn))
            localStorage.setItem('token',resp.data.idToken);
            const expirationDate=new Date(new Date().getTime()+resp.data.expiresIn*1000)
            localStorage.setItem('expiresIn',expirationDate);
            localStorage.setItem('userId',resp.data.localId)
            
        })
        .catch(err=>{
            console.log(err.response);
            dispatch(authFail(err.response.data.error))

        })


    }
}


export const setAuthRedirectPath=(path)=>{

    return{

        type:actionTypes.SET_AUTH_REDIRECT_PATH,
        path:path
        
    }
}


export const authCheckState=()=>{

    return dispatch=>{

        const token=localStorage.getItem('token')

        if(!token){
            console.log('44444444444444444444444');
            dispatch(logout())
        }

        else{

            const expirationDate=new Date( localStorage.getItem('expiresIn'));

            if(expirationDate>new Date()){

                const userId=localStorage.getItem('userId')
                dispatch(authSuccess(token,userId))
                dispatch(checkAuthTimeout((expirationDate.getTime()-new Date().getTime())/1000))
            }
            else{
                console.log('1111111111111111111111111');
                dispatch(logout())
            }


        }
    }
}


