import React, { Component } from 'react';
import {Route,Switch} from 'react-router-dom';
import Layout from './hoc/Layout/Layout';
import BurgerBuilder from './containers/BurgerBuilder/BurgerBuilder';
// import Checkout from './containers/Checkout/Checkout';
// import Orders from './containers/Orders/Orders';
// import Auth from '../src/containers/Auth/Auth';
import Logout from '../src/containers/Auth/logout/Logout';
import {connect} from 'react-redux';
import * as action from './store/actions/index';
import asyncComponent from './hoc/Async/AsyncLoad';



const asyncCheckOut=asyncComponent(()=>{return import('./containers/Checkout/Checkout')});
const asyncOrders=asyncComponent(()=>{return import('./containers/Orders/Orders')}); 
const asyncAuth=asyncComponent(()=>{return import('../src/containers/Auth/Auth')}); 



class App extends Component {

  componentDidMount(){

    this.props.onTryAutoSignUp()
  }


  render () {

    let routes=( 
      <Switch>
       <Route path="/auth" component={asyncAuth}></Route>
       <Route path="/" exact component={BurgerBuilder}></Route>
       </Switch>
       );

    if(this.props.isAuth){

      routes=
      <Switch>

      <Route path="/" exact component={BurgerBuilder}></Route>
      <Route path="/checkout"  component={asyncCheckOut}></Route>
      <Route path="/orders" component={asyncOrders}></Route>
      <Route path="/logout" component={Logout}></Route>
      <Route path="/auth" component={asyncAuth}></Route>
      </Switch>
    
    }
    return (
      <div>
        <Layout>
          {routes}
         
                    {/* <BurgerBuilder /> */}
          {/* <Checkout></Checkout> */}
        </Layout>
      </div>
    );
  }
}

const mapStateToProps=(state)=>{

  return{
    isAuth:state.auth.token!=null
  }
}



const mapDispatchToProps=(dispatch)=>{

  return{

    onTryAutoSignUp:()=>dispatch(action.authCheckState())
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



